<template>
  <div class="list">
      <div v-if="isLoadingAdsWithPagination">isLoading</div>
      <div v-else class="table">
          <div class="table__top">
              <button @click="setTableOptionsToDefault">Reload</button>
          </div>

          <div class="table__wrapper">
              <div class="table__row table__row-header">
                  <div
                          v-for="header in headers"
                          :style="`width:${header.width}`"
                          :key="header.value"
                          class="table__item table__item-header"
                          @click="header.sortable === true ? setSortBy(header.value) : ''"
                  >
                      {{ header.title }}
                      <div
                              v-if="tableOptions.sortBy === header.value"
                              class="sort-arrow"
                              :class="{'rotate':tableOptions.sortDesc === true}"
                      >
                          <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor"
                               class="bi bi-arrow-up-short" viewBox="0 0 20 20">
                              <path fill-rule="evenodd"
                                    d="M8 12a.5.5 0 0 0 .5-.5V5.707l2.146 2.147a.5.5 0 0 0 .708-.708l-3-3a.5.5 0 0 0-.708 0l-3 3a.5.5 0 1 0 .708.708L7.5 5.707V11.5a.5.5 0 0 0 .5.5z"/>
                          </svg>
                      </div>
                  </div>
              </div>


              <router-link
                      :to="{name: 'adv', params: { id: item.id}}"
                      class="table__row"
                      v-for="item in adsWithPagination"
                      :key="item.id"
              >

                  <div
                          v-for="header in headers"
                          :key="header.value"
                          class="table__item"
                          :style="`width:${header.width}`"
                  >
                      {{ item[header.value] }}
                  </div>

              </router-link>
          </div>

          <div class="pagination">
                  <div class="pagination__item">
                      {{ paginationCountText }}
                  </div>
                  <div @click="minusPage" class=" pagination__item pagination__item-button">
                      <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor"
                           class="bi bi-chevron-left" viewBox="0 0 16 16">
                          <path fill-rule="evenodd"
                                d="M11.354 1.646a.5.5 0 0 1 0 .708L5.707 8l5.647 5.646a.5.5 0 0 1-.708.708l-6-6a.5.5 0 0 1 0-.708l6-6a.5.5 0 0 1 .708 0z"/>
                      </svg>
                  </div>
                  <select v-model="currentPage" class="pagination__item">
                      <option v-for="(page, index) in pages" :key="index">
                          {{ page }}
                      </option>
                  </select>
                  <div @click="plusPage" class="pagination__item pagination__item-button">
                      <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor"
                           class="bi bi-chevron-right" viewBox="0 0 16 16">
                          <path fill-rule="evenodd"
                                d="M4.646 1.646a.5.5 0 0 1 .708 0l6 6a.5.5 0 0 1 0 .708l-6 6a.5.5 0 0 1-.708-.708L10.293 8 4.646 2.354a.5.5 0 0 1 0-.708z"/>
                      </svg>
                  </div>
              </div>
          </div>
  </div>
</template>

<script>
    import {mapActions, mapGetters, mapMutations, mapState} from "vuex";

    export default {
        name: "listView",
        data() {
            return {
                headers: [
                    {title: 'id', value: 'id', width: '4%', sortable: true},
                    {title: 'Название', value: 'name', width: '30%', sortable: true},
                    {title: 'Цена', value: 'price', width: '20%', sortable: true},
                    {title: 'Ссылка', value: 'image', width: '46%', sortable: false}
                ],
            }
        },
        computed: {
            ...mapState(['adsWithPagination', 'isLoadingAdsWithPagination', 'tableOptions', 'total']),
            ...mapGetters(['lastPage']),
            currentPage:{
                get() {
                    return this.tableOptions.page
                },
                set(value) {
                    this.setPage(value)
                }
            },
            paginationCountText() {
                return `${this.total === 0 ? 0 : 10 *(this.tableOptions.page - 1) + 1}
                 -
                ${10 * this.tableOptions.page < this.total ? 10 * this.tableOptions.page : this.total}
                из
                ${this.total}`
            },
            lastPage() {
                return Math.ceil(this.total / 10)
            },
            pages() {
                return Array.from({length: this.lastPage}, (v, k) => k + 1)
            }
        },
        watch: {
            tableOptions:{
                deep: true,
                handler() {
                    this.loadAds()
                }
            },
        },
        created() {
            this.setTableOptionsToDefault()
            this.loadAds()
        },
        methods: {
            ...mapMutations(['setTableOptionsToDefault', 'setSortBy', 'setPage']),
            ...mapActions(['loadAds']),
            plusPage() {
                if (this.tableOptions.page < this.lastPage) {
                    this.currentPage++
                }
            },
            minusPage() {
                if (this.tableOptions.page > 1) {
                    this.currentPage--
                }
            }
        }
    }
</script>
